import test from 'ava';
import mongoose from 'mongoose';
import User from '../../src/user';
import {
  mongooseConnect,
  mongooseDisconnect,
} from '../test_helper';

test.before('connect to database', async (t) => {
  await mongooseConnect('users_test');
});

test.after.always('disconnect from database', async () => {
  await mongooseDisconnect();
});

test.serial('Make user name required', async (t) => {
  const user = new User({ name: undefined });
  const validationResult = user.validateSync();
  const { message } = validationResult.errors.name;

  t.is( message, 'Name is required.' )

  console.log('validationResult');
  console.log(validationResult);

  const user2 = new User({ name: 'user2' });
  const validationResult2 = user2.validateSync();


  t.pass();
});